.logo
    align-items: center
    display: flex
    height: 30px
    justify-content: center
    width: 50px

header
    align-items: center
    display: flex
    justify-content: space-between
    position: fixed
    width: 100%
    z-index: 2
    background: #fff !important
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.1)
    .logo
        background: rgba(255, 255, 255, 0.5)
        color: #fff

footer
    .logo
        background: rgba(0, 0, 0, 0.2)
        color: #000
    
.anticon
    svg
        height: 2em
        width: 2em